@import "../variables.scss";

.nav{
    justify-content: space-between;
    align-items: center;
    padding: 20px;

    .nav-image {
        width: 500px;
        height: 250px;
}
a {
        color: white;
        display: inline-flex;
        padding-left: 10%;
        padding-right: 10%;
        font-size: 1.5rem;
        margin: 5px 0;
        
    }
    .menu-toggle{
        display: none;
        background-color: transparent;
        border: none;
        cursor: pointer;
    }
    a:link {
        text-decoration-line: none;
        cursor: pointer;
        
    }
    a:hover{
        color: rgb(228, 105, 53);
    }
    .logout-button {
        font-family: 'Inter', sans-serif;
        background: $background-color-buttons;
        width: 120px;
        height: 2rem;
        color: $white-font;
        font-size: 20px;
        border-radius: 2rem;
        margin-top: 1.8rem;
        border: none;
        box-shadow: 3px 3px 3px 1px rgb(121, 116, 116);  
    }
    .logout-button:hover{
        background: rgb(9, 67, 13);
    }
    .centered {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .nav-links{
        display:none;
    }
    .show-menu .nav-links{
        display: flex;
    }
    

    @media screen and (max-width: 500px){
       .nav-menu-button{
            display: block;
       }
       .menu-toggle{
        display: block;
       }
       .menu-toggle img{
        width: 50px;
        margin-top: 20px;
       }

       .show-menu .nav-links{
        display: flex;
        flex-direction: column;
       }

       .nav-links a {
        display: none;
      }
      .nav-links a {
        margin: 5px 0;
      }
    
      .show-menu .nav-menu-button {
        font-size: 20px;
        margin-top: 10px;
      }
    
    
      .show-menu .nav-links {
        display: flex;
      }
        .nav-image {
            width: 280px;
            height: 170px;
    }
         a{
            display: block;
            cursor: pointer;
            width: 70%;
            font-size: 20px;
    }

    nav .logout-button{
        display: inline;
        

    }
    .logout-button{
        width: 90px;
        height: 1rem;
        font-size: 15px;
    }
  
}

    @media screen and (max-width: 850px){
            .nav-links {
                justify-content: space-between;
                display: flex;
               padding: 0;
               margin: 0;
                  
            }
            .logout-button{
                display: flex;
                
            }
            }
            @media screen and (max-width: 1200px){
                .nav-links {
                    display: flex;
                    width: 80%;   
                }
                .logout-button{
                    display: flex;
                    
                }
                }
                @media screen and (max-width: 1500px){
                    .nav-links {
                        display: flex;
                        width: 80%;   
                    }
                    .logout-button{
                        display: flex;
                        
                    }
                    }
                    @media screen and (max-width: 1900px){
                        .nav-links {
                            display: flex;
                            width: 80%;   
                        }
                        .logout-button{
                            display: flex;
                            
                        }
                        }
}
